h1 {
  font-family: 'SEGA LOGO FONT';
  font-weight: 200;
  color: #0ccac4;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #80bea7;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #80bea7;
}

